import axios from 'axios'
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    lista:[],
    pokemon:null    
  },
  mutations: {
    agregarLista:(state, response)=>{
      state.lista = response
    },
    modificarPkmn:(state, response)=>{
      state.pokemon= response
    }
  },
  actions: {
    getList: (context)=>{
      axios.get('https://pokeapi.co/api/v2/pokemon?limit=150')
      .then((response) =>{
        context.commit('agregarLista', response.data.results)
      })
    },
  getPokemon:(context, url)=>{
      axios.get(url)
      .then((response)=>{
        context.commit('modificarPkmn', response.data)
      })
    }
  }
  ,
})