<template>
  <div id="app">
    <div>
    <img id="logo" src="https://upload.wikimedia.org/wikipedia/commons/thumb/9/98/International_Pok%C3%A9mon_logo.svg/2560px-International_Pok%C3%A9mon_logo.svg.png" alt=""><br>
      <div class="header">
        <div class="search">
          <label for="name">Nombre: </label>
          <input v-model="nombre_pokemon" type="text" placeholder="Buscar pokémon...">
          <button class="submit btn-buscar" @click="consulta"><i class="fas fa-search"></i></button>
        </div>
      </div>
      <div class="pokemon pt-5">
          <h3 class="nombre mb-5">{{name}}</h3>
          
            <div class="d-flex container-fluid mx-auto justify-content-center pkdx-top">
                <div class="row ms-auto me-auto col-6 pkdx pkdx-top">
                  <div class="buttons d-flex justify-content-end mb-3">
                    <button type="button" class="btn px-1 mx-2 btn-success rounded-circle col-1"></button>
                    <button type="button" class="btn px-1 mx-2 btn-danger rounded-circle col-1"></button>
                    <button type="button" class="btn px-1 mx-2 btn-warning rounded-circle col-1"></button>
                  </div>          
                  <div class="col-2 img ms-auto">
                    <img :src="apariencia">
                  </div>
                  <div class="col-7 ms-2 me-auto box-display">
                    <h2 class="titulo">Habilidades</h2>     
                    <div class="lista">
                      <ul >
                        <li class="col-6" v-for="(habilidad, index) in habilidades" :key="index">
                          ○ {{habilidad}}
                        </li>
                      </ul>
                    </div>
                  </div>
              </div>
            </div>
            
        

          <div class="container pkdx pkdx-bttm">
            <div class="box-display col-10 mx-auto my-3">
              <h2 class="titulo">Movimientos</h2>
              <div class="lista ">
                <ul>
                  <li v-for="(movimiento, index) in movimientos" :key="index">
                    ○ {{movimiento}}
                  </li>
                </ul>
              </div> 
            </div>
          </div> 
      </div>
    </div>

  </div>
</template>

<script>
import axios from "axios"

/* import {mapState} from 'vuex' */
export default {


  name: 'App',
   created(){
    this.consulta()
  },
  data(){
    return{
      nombre_pokemon:"",
      nombre: this.nombre_pokemon,
      datos:null,
    }
  },
  computed: {
    apariencia(){
      let url=""
      if(this.datos == null){
        return url
      }
      url = this.datos.sprites.front_default
      return url
    },
    habilidades(){
      let hab = []
      if(this.datos == null){
        return hab
      }
      else{
        for (let i = 0; i < this.datos.abilities.length; i++) {
        const habilidad = this.datos.abilities[i].ability.name;
        hab.push(habilidad)
      }
      }
      
      return hab
    },
    
    movimientos(){
      let mov = []
      if(this.datos == null){
        return mov
      }
      else{
        for (let i = 0; i < this.datos.moves.length; i++) {
          const movimiento = this.datos.moves[i].move.name;
          mov.push(movimiento)
      }}
      
      return mov
    },
    name(){
      let name = ""
      if(this.datos == null){
        return name
      }
      name = this.datos.name
      let nombre_capitalized = name.charAt(0).toUpperCase() + name.slice(1)
      return nombre_capitalized
    },
    pokemon(){
      return this.$store.state.pokemon
    }

  },
  methods:{
    actualizarData(){
      console.log(this.pokemon)
      this.datos= this.pokemon
    },
    consulta(){
        if (this.nombre_pokemon == "") {
          axios.get(`https://pokeapi.co/api/v2/pokemon/pikachu`)
        .then((response)=>{
          this.datos=response.data
        })
        .catch(()=>
          alert("Pokemon no encontrado! por favor revisa que el nombre esté escrito correctamente."))
        }
        else{
        let pokemon = this.nombre_pokemon.toLowerCase()
        axios.get(`https://pokeapi.co/api/v2/pokemon/${pokemon}`)
        .then((response)=>{
          this.datos=response.data
        })
        .catch(()=>
          alert("Pokemon no encontrado! por favor revisa que el nombre esté escrito correctamente."))
        }
    },
    llamarLista(){
      this.$store.dispatch('getList')
    }
  },
}
</script>

